@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Accordion {
    position: relative;
    height: 100%;
    width: 320px;
}

.question {
    font-size: 12px;
    font-weight: 400;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    cursor: pointer;
    transition: color 0.3s;
}

.question-active {
    font-weight: 700;
}

.question:hover {
    color: hsl(14, 88%, 65%);
}

.arrow-btn {
    color: #f87958;
    transition: all 0.4s ease;
}

.arrow-btn-active {
    -webkit-transform: rotateZ(-180deg);
            transform: rotateZ(-180deg);
}

.answer {
    font-size: 12px;
    color: hsl(237, 12%, 33%);
    padding-top: 15px;
    line-height: 20px;
    opacity: 0;
    overflow: hidden;
    max-height: 0;
    transition: all 0.4s ease-in-out;
}

.accordion-element {
    padding: 15px 0;
    border-bottom: 1px solid hsl(240, 5%, 91%);
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 100%, 100%;
    -webkit-align-items: center;
            align-items: center;
    max-height: 46.59px;
    transition: all 0.4s ease-in-out;
}

.open-answer {
    max-height: 1000px;
    display: block;
    opacity: 1;
}

.open-accordion-element {
    max-height: 120px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Kumbh Sans', sans-serif;
}

.App {
    height: 100vh;
    background: linear-gradient(
        to top right,
        hsl(240, 73%, 65%),
        hsl(273, 75%, 66%)
    );
}

.card {
    width: 925px;
    height: 500px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-radius: 20px;
    box-shadow: 0 25px 30px rgba(0, 0, 0, 0.3);
    padding: 70px 90px 80px 0;
}

.card {
    background: url(/static/media/illustration-woman-online-desktop.843eb114.svg) left
            center no-repeat,
        url(/static/media/bg-pattern-desktop.f51e07a2.svg) left center no-repeat, #fff;
    background-size: 50%, 100%;
    background-position: -80px, -540px -270px;
    position: relative;
}

.box-img-element {
    top: 40%;
    left: -10%;
    position: absolute;
}

.accordion-block-headline {
    text-transform: uppercase;
    margin-bottom: 45px;
    color: #1e1e36;
}

